"use strict";

import { arrayify } from "@ethersproject/bytes";
import { Logger } from "@ethersproject/logger";
import { version } from "./_version";
var logger = new Logger(version);
var anyGlobal = null;

try {
  anyGlobal = window;

  if (anyGlobal == null) {
    throw new Error("try next");
  }
} catch (error) {
  try {
    anyGlobal = global;

    if (anyGlobal == null) {
      throw new Error("try next");
    }
  } catch (error) {
    anyGlobal = {};
  }
}

var crypto = anyGlobal.crypto || anyGlobal.msCrypto;

if (!crypto || !crypto.getRandomValues) {
  logger.warn("WARNING: Missing strong random number source");
  crypto = {
    getRandomValues: function getRandomValues(buffer) {
      return logger.throwError("no secure random source avaialble", Logger.errors.UNSUPPORTED_OPERATION, {
        operation: "crypto.getRandomValues"
      });
    }
  };
}

export function randomBytes(length) {
  if (length <= 0 || length > 1024 || length % 1) {
    logger.throwArgumentError("invalid length", "length", length);
  }

  var result = new Uint8Array(length);
  crypto.getRandomValues(result);
  return arrayify(result);
}
;